@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
body {
  background-color: #eaebec;
  font-family: "Source Sans Pro", sans-serif;
  font-style: normal;
  font-size: 20px;
  color: #222222;
}

h1,
h2,
h3,
h4,
h5 {
  color: #fff;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  line-height: 1.3;
  text-transform: uppercase;
  text-align: center;
}

.img-responsive {
  margin: 0 auto;
}

p {
  color: #222222;
  line-height: 1.3;
  word-wrap: break-word;
}

a {
  color: #440784;
  line-height: 1.3;
  text-decoration: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

a:hover,
a:active,
a:focus {
  color: #440784;
  text-decoration: underline;
}

::-moz-selection {
  background: #000;
  color: #fff;
}

::selection {
  background: #000;
  color: #fff;
}
.intro {
  background: #041c82;
  background: -webkit-gradient(linear, left top, left bottom, from(#041c82), color-stop(65%, #00122b), to(#440784));
  background: -webkit-linear-gradient(top, #041c82 0%, #00122b 65%, #440784 100%);
  background: linear-gradient(to bottom, #041c82 0%, #00122b 65%, #440784 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#041c82", endColorstr="#440784",GradientType=0 );
  padding: 40px 0px;
}

.intro h1 {
  text-align: center;
  font-weight: bold;
  font-size: 42px;
}

.intro h1 span {
  display: block;
}

.intro h2 {
  text-align: center;
  font-weight: normal;
  font-size: 32px;
}

.intro .image-container {
  padding-top: 40px;
  max-width: 480px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .intro {
    background: #041c82;
    background: -webkit-linear-gradient(45deg, #041c82 0%, #00122b 65%, #440784 100%);
    background: linear-gradient(45deg, #041c82 0%, #00122b 65%, #440784 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#041c82", endColorstr="#440784",GradientType=1 );
  }
  .intro .flex-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  .intro h1 {
    text-align: left;
    font-size: 50px;
    margin-top: 0px;
  }
  .intro h2 {
    text-align: left;
    font-size: 38px;
    margin-bottom: 0px;
  }
  .intro .image-container {
    padding-top: 0px;
    max-width: none;
  }
}

.portfolio {
  padding: 40px 0px;
}

.portfolio h3 {
  color: #041c82;
  text-align: center;
  font-size: 30px;
}

.portfolio .portfolio-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}

.portfolio .portfolio-container .project-card {
  border-radius: 0px;
  box-shadow: 0px 13px 21px -5px rgba(0, 0, 0, 0.4);
  width: 100%;
  max-width: 480px;
  margin: 15px auto;
}

.portfolio .portfolio-container .project-card .image-container {
  background-color: #fff;
  padding: 15px;
  min-height: 125px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.portfolio .portfolio-container .project-card .info-container {
  text-align: center;
  padding: 15px;
  background: #041c82;
  background: -webkit-linear-gradient(45deg, #041c82 0%, #00122b 65%, #440784 100%);
  background: linear-gradient(45deg, #041c82 0%, #00122b 65%, #440784 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#041c82", endColorstr="#440784",GradientType=1 );
}

.portfolio .portfolio-container .project-card .info-container p {
  margin: 0px;
  color: #fff;
}

.portfolio .portfolio-container .project-card .info-container p.name {
  font-weight: bold;
}

.portfolio .portfolio-container .project-card .info-container p.name span {
  display: block;
}

.portfolio .portfolio-container .project-card .info-container p.description {
  font-weight: 300;
}

.portfolio .portfolio-container .project-card .info-container .link-container {
  text-align: center;
  padding: 15px 0px;
}

.portfolio .portfolio-container .project-card .info-container .link-container a {
  color: #fff;
}

.portfolio .portfolio-container .project-card .stack-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 15px 0px 0px;
  border-top: 1px solid #fff;
}

.portfolio .portfolio-container .project-card .stack-container .stack {
  flex: 1 1;
  padding: 8px;
}

@media (min-width: 768px) {
  .portfolio h3 {
    font-size: 34px;
  }
  .portfolio .portfolio-container {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .portfolio .portfolio-container .project-card {
    width: calc(50% - 30px);
  }
}

@media (min-width: 992px) {
  .portfolio .portfolio-container .project-card {
    width: calc(33.33% - 30px);
    margin: 15px;
    max-width: none;
  }
}

